import React, { Component } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
  MDBCollapse,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem
} from "mdbreact";
import Logo from '../imgs/nmb_icon.png';
import SideNav from './SideNav'

class NavBar extends Component {
 
  render() {
    return (
      <div>
      <SideNav />
      <MDBNavbar light expand="md" fixed="top">
        <MDBNavbarBrand>
          <img id="navbar_logo" alt="NMB Logo" src={Logo}/>
        </MDBNavbarBrand>
        <MDBCollapse navbar>
          <MDBNavbarNav left>
           
            <MDBNavItem>
             <MDBNavLink to="/my-app/">Home</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBDropdown>
                <MDBDropdownToggle nav caret>
                  <span className="mr-2">Trip Tracker</span>
                </MDBDropdownToggle>
                <MDBDropdownMenu>
                  <MDBDropdownItem href="/my-app/whos-on-track">Who's On Track?</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/ytd-agent">YTD Agent</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/ytd-agency">YTD Agnecy</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/ytd-independents">YTD Independents</MDBDropdownItem>
                </MDBDropdownMenu>
              </MDBDropdown>
            </MDBNavItem>
            <MDBNavItem>
             <MDBNavLink to="/my-app/2020-fast-start-trips">2020 Fast Start Trips</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBDropdown>
                <MDBDropdownToggle nav caret>
                  <span className="mr-2">Details</span>
                </MDBDropdownToggle>
                <MDBDropdownMenu>
                  <MDBDropdownItem href="/my-app/manager-qualifications">Manager Trip Qualifications</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/fast-start-manager-qualifications">Fast Start Manager Trip Qualifications</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/new-reps">New Reps</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/trip-credits">Trip Credits</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/accomodations">Accomodations</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/awards">Awards</MDBDropdownItem>
                  <MDBDropdownItem href="/my-app/terms">Terms</MDBDropdownItem>
                </MDBDropdownMenu>
              </MDBDropdown>
            </MDBNavItem>
            <MDBNavItem>
             <MDBNavLink to="/my-app/dashboard">Dashboard</MDBNavLink>
            </MDBNavItem>
          </MDBNavbarNav>
          <MDBNavbarNav right>
          <MDBNavItem>
             <MDBNavLink to="/my-app/Login">Log In</MDBNavLink>
            </MDBNavItem>
          </MDBNavbarNav>
          
        </MDBCollapse>
      </MDBNavbar>
      </div>
    );
  }
}

export default NavBar;
